.ContainerStyle {
  width: 100%;
  background: blue;
  min-width: 900px;
}

.WrapperStyle {
  width: 900px;
  background-color: white;
  margin: 0 auto;
  position: relative;
}


.BtnStyle1  {
    padding: 10px 0;
    position: absolute;
    left: 0;
  }

.BtnStyle2  {
    padding: 10px 0;
    position: absolute;
    right: 0;
  }

.ClockStyle  {
    font-size: 10em;
    color: green;
    transform: translateY(10%);
    
  }

  @media only screen and (max-width:740px) {
    * {
      margin-top: 40px;
    }
    .ClockStyle {
        color: blue;
        width: 90%;
        font-size: 50px;
    }
    .ContainerStyle{
      min-width:90%;
      width: 90%;
    }

    .WrapperStyle{
      width: 100%;
    }

    html, body {
      max-width: 100%;
      overflow-x: hidden;
      }

}